"use client";
import { useEffect, useState } from "react";

const fmt = (d) => d ? new Date(d).toLocaleString("fr-FR") : "-";

export default function ClientMessaging({ clientId, dossiers, senderType = "Op√©rateur" }) {
  const [selectedDossier, setSelectedDossier] = useState(null);
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState("");
  const [sending, setSending] = useState(false);
  const [loading, setLoading] = useState(false);

  // D√©termine qui envoie le message
  const isClientSending = senderType === "Client";
  const isOperatorSending = senderType === "Op√©rateur";

  // S√©lectionner le premier dossier par d√©faut
  useEffect(() => {
    if (dossiers.length > 0 && !selectedDossier) {
      setSelectedDossier(dossiers[0]);
    }
  }, [dossiers, selectedDossier]);

  // Charger messages du dossier s√©lectionn√©
  useEffect(() => {
    if (!selectedDossier) return;
    
    setLoading(true);
    fetch(`/api/dossiers/${selectedDossier.id}/messages`, { cache: "no-store" })
      .then(r => r.ok ? r.json() : Promise.reject(r))
      .then(data => setMessages(data.messages || []))
      .catch(e => {
        console.error("Erreur chargement messages:", e);
        setMessages([]);
      })
      .finally(() => setLoading(false));
  }, [selectedDossier]);

  // Envoyer nouveau message (adapt√© selon le sender)
  async function sendMessage() {
    if (!newMessage.trim() || !selectedDossier) return;
    
    setSending(true);
    try {
      const r = await fetch(`/api/dossiers/${selectedDossier.id}/messages`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          sender_type: senderType, // üî• Utilise la prop
          body: newMessage.trim(),
          send_email: isOperatorSending // Email seulement si op√©rateur envoie
        })
      });
      
      if (!r.ok) throw new Error(`HTTP ${r.status}`);
      
      const data = await r.json();
      
      // Recharger les messages
      setMessages(prev => [...prev, data.message]);
      setNewMessage("");
      
      // Alerte diff√©rente selon qui envoie
      if (isOperatorSending && data.email_sent) {
        alert(`Message envoy√© et email automatique exp√©di√© √† ${data.client_email}`);
      } else if (isClientSending) {
        alert("Message envoy√© √† l'√©quipe !");
      }
    } catch (e) {
      console.error("Erreur envoi message:", e);
      alert("Erreur lors de l'envoi du message");
    } finally {
      setSending(false);
    }
  }

  const getSenderColor = (senderType) => {
    switch (senderType) {
      case "Client": return "bg-blue-50 border-l-4 border-l-blue-500";
      case "Op√©rateur": return "bg-green-50 border-l-4 border-l-green-500";
      case "Syst√®me": return "bg-gray-50 border-l-4 border-l-gray-500";
      default: return "bg-gray-50";
    }
  };

  const getSenderIcon = (senderType) => {
    switch (senderType) {
      case "Client": return "üë§";
      case "Op√©rateur": return "üë®‚Äçüíº";
      case "Syst√®me": return "ü§ñ";
      default: return "üí¨";
    }
  };

  // Affichage du nom de l'exp√©diteur selon le contexte
  const getSenderDisplayName = (msgSenderType) => {
    if (isClientSending) {
      // Vue client : "Vous" pour ses messages, "√âquipe" pour les op√©rateurs
      return msgSenderType === "Client" ? "Vous" : 
             msgSenderType === "Op√©rateur" ? "√âquipe" : 
             msgSenderType;
    } else {
      // Vue op√©rateur : noms normaux
      return msgSenderType;
    }
  };

  if (dossiers.length === 0) {
    return (
      <section className="border rounded p-4">
        <h3 className="text-lg font-semibold mb-3">üí¨ Messagerie</h3>
        <p className="text-gray-600">Aucun dossier trouv√©{isClientSending ? "" : " pour ce client"}.</p>
      </section>
    );
  }

  return (
    <section className="border rounded p-4 space-y-4">
      <h3 className="text-lg font-semibold">
        üí¨ {isClientSending ? "Messagerie avec votre √©quipe" : "Messagerie Client"}
        <span className="text-sm font-normal text-gray-600 ml-2">
          {isClientSending 
            ? "(Communication avec nos op√©rateurs)" 
            : "(Communication directe avec le client)"
          }
        </span>
      </h3>

      {/* S√©lecteur de dossier */}
      {dossiers.length > 1 && (
        <div>
          <label className="block text-sm font-medium mb-1">
            Dossier √† afficher :
          </label>
          <select
            className="border rounded p-2"
            value={selectedDossier?.id || ""}
            onChange={(e) => {
              const dossier = dossiers.find(d => d.id === parseInt(e.target.value));
              setSelectedDossier(dossier);
            }}
          >
            {dossiers.map(d => (
              <option key={d.id} value={d.id}>
                Dossier #{d.id} - {d.statut}
              </option>
            ))}
          </select>
        </div>
      )}

      {selectedDossier && (
        <>
          <div className="text-sm bg-blue-50 p-2 rounded">
            üìÅ <strong>Dossier #{selectedDossier.id}</strong> - Statut: {selectedDossier.statut}
          </div>

          {/* Messages */}
          <div className="space-y-3 max-h-96 overflow-y-auto">
            {loading ? (
              <p className="text-gray-600">Chargement des messages...</p>
            ) : messages.length === 0 ? (
              <p className="text-gray-600">Aucun message pour ce dossier.</p>
            ) : (
              messages.map(msg => (
                <div 
                  key={msg.id} 
                  className={`p-3 rounded ${getSenderColor(msg.sender_type)}`}
                >
                  <div className="flex items-center justify-between mb-1">
                    <div className="flex items-center gap-2">
                      <span>{getSenderIcon(msg.sender_type)}</span>
                      <strong className="text-sm">
                        {getSenderDisplayName(msg.sender_type)}
                      </strong>
                    </div>
                    <span className="text-xs text-gray-600">
                      {fmt(msg.at)}
                    </span>
                  </div>
                  <div className="whitespace-pre-wrap">{msg.body}</div>
                </div>
              ))
            )}
          </div>

          {/* Nouveau message */}
          <div className="border-t pt-4 space-y-3">
            <h4 className="font-medium">
              {isClientSending ? "‚úâÔ∏è Envoyer un message √† l'√©quipe" : "‚úâÔ∏è R√©pondre au client"}
            </h4>
            <textarea
              className="w-full border rounded p-3 h-24"
              placeholder={isClientSending 
                ? "Votre message √† l'√©quipe..." 
                : "Votre message sera envoy√© au client par email..."
              }
              value={newMessage}
              onChange={(e) => setNewMessage(e.target.value)}
              disabled={sending}
            />
            <div className="flex gap-2">
              <button
                className="bg-blue-600 text-white px-4 py-2 rounded disabled:opacity-50"
                onClick={sendMessage}
                disabled={sending || !newMessage.trim()}
              >
                {sending ? "Envoi..." : 
                 isClientSending ? "üì§ Envoyer √† l'√©quipe" : "üìß Envoyer au client"}
              </button>
              <button
                className="border px-4 py-2 rounded"
                onClick={() => setNewMessage("")}
                disabled={sending}
              >
                Effacer
              </button>
            </div>
            <p className="text-xs text-gray-600">
              üí° {isClientSending 
                ? "Votre message sera envoy√© √† l'√©quipe et ajout√© √† l'historique de votre dossier." 
                : "Le message sera ajout√© √† l'historique et envoy√© automatiquement par email au client."
              }
            </p>
          </div>
        </>
      )}
    </section>
  );
}