"use client";
const fmt = (d) => (d ? new Date(d).toISOString().slice(0,10) : "‚Äî");
const show = (v) => (v ?? v === 0 ? String(v) : "‚Äî");

/**
 * props:
 * - pieces: [{type, fichier, statut, motif_refus, uploaded_at}]
 * - downloads: [{type, fichier}]
 * - requiredTypes?: ["Passeport","PhotoIdentite","RIB","CNI"] (optionnel)
 */
export default function PiecesJustificativesCard({ pieces = [], downloads = [], requiredTypes = [] }) {
  // Comptes d√©riv√©s de la BDD (pas de valeurs par d√©faut)
  const total = pieces.length;
  const valides = pieces.filter(p => p.statut === "valide").length;

  const missing = requiredTypes.length
    ? requiredTypes.filter(t => !pieces.some(p => p.type === t))
    : [];

  return (
    <section>
      <h3 className="text-lg font-semibold mb-3">üìÑ Pi√®ces justificatives</h3>
      <div className="bg-white border rounded p-4">
        {/* √âtat synth√©tique sans forcer: on affiche ce que dit la BDD */}
        <div className="text-sm mb-3">
          <strong>Statut:</strong> {valides}/{total} valid√©es
          {requiredTypes.length ? (
            <span className="ml-2">
              ‚Äî Requises manquantes: {missing.length ? missing.join(", ") : "‚Äî"}
            </span>
          ) : null}
        </div>

        <ul className="list-disc pl-6">
          {total ? pieces.map((p, i) => (
            <li key={i}>
              {show(p.type)} ‚Äî {show(p.fichier)} ‚Äî {show(p.statut)}
              {p.motif_refus ? ` (${p.motif_refus})` : ""} {p.uploaded_at ? `(${fmt(p.uploaded_at)})` : ""}
            </li>
          )) : <li>‚Äî</li>}
        </ul>

        <h4 className="font-medium mt-3">T√©l√©chargements disponibles</h4>
        <ul className="list-disc pl-6">
          {downloads.length ? downloads.map((d, i) => (
            <li key={i}>{show(d.type)} ‚Äî {show(d.fichier)}</li>
          )) : <li>‚Äî</li>}
        </ul>
      </div>
    </section>
  );
}
